.login-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 300px;
    margin: 0 auto;
    font-family: Tahoma, Geneva, sans-serif;
    gap: 30px;
  }
  
  .input-name {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    width: 100%;
    height: 30px;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 5px;
  }

  .select-user {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    width: 100%;
    height: 30px;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 5px;
  }